.orgBoard {
  width: 1800px;
  margin: auto;
}
#orgBoardHeader {
  width: 1800px;
  margin: auto;
  font-family: 'Roboto', sans-serif;
}
.Node {
  width: 235px;
  display: inline-block;
  background-color: #9ec4f0;
  border-radius: 5px;
  font-family: 'Cuprum', sans-serif;
}
.targetDept {
  min-height: 46em;
  max-height: 46em;
  position: relative;
  font-size: 0.65em;
  padding: 2px;
  margin-bottom: 2em;
  overflow-y: scroll;
  -ms-overflow-style: none;
  /* IE and Edge */
  scrollbar-width: none;
  /* Firefox */
  font-family: 'Roboto', sans-serif;
}
.targetDept::-webkit-scrollbar {
  display: none;
}
.card-container {
  font-size: 0.78em;
  min-height: 46em;
  overflow: hidden;
}
#DivisionOne,
#DivisionFive {
  margin-top: -1em;
  min-height: 46em;
  max-height: 46em;
}
.deptVFP {
  position: absolute;
  bottom: 0;
  padding: 2px;
  text-align: 'center';
  font-size: 0.6em;
}
.vfpContainer {
  position: relative;
  overflow: visible;
}
.divVfp {
  font-size: 0.6em;
  padding: 2px;
  height: 65px;
}
.vfp {
  color: #dddddd;
  margin: auto;
  font-family: 'Roboto', sans-serif;
}
#owner {
  overflow: visible;
  position: relative;
  z-index: 99;
}
#owner .nodeGroupLineVerticalMiddle {
  height: 100px;
  width: 20%;
}
* {
  box-sizing: border-box;
}
.Board {
  font-family: sans-serif;
  white-space: nowrap;
}
.Column {
  display: inline-block;
  vertical-align: top;
  width: 225px;
  padding: 10px 15px;
  border-radius: 5px;
  margin-right: 20px;
  background: #ffffff;
  color: #bfdbf7;
}
.Column__title {
  font-size: 18px;
  margin-bottom: 10px;
}
.Card {
  padding: 5px;
  background-color: #dfdfdf;
  color: #212121;
  box-shadow: 4px 4px 12px 2px #a4a4a4;
  border-radius: 5px;
  margin-bottom: 10px;
  cursor: move;
}
.Card--spacer,
.Card--dragging {
  opacity: 0;
}
.Card--spacer {
  height: 60em;
}
.TextForm__input {
  display: block;
  background: #f4fffd;
  width: 100%;
  border-radius: 3px;
  border: none;
  font-size: 16px;
  padding: 5px;
  margin-bottom: 5px;
  opacity: 0.7;
}
.TextForm__input:focus {
  opacity: 1;
}
.oicGraphs {
  color: #000;
  background-color: #dfdfdf;
  box-shadow: 0px 0px 15px 0px #888888;
  border-radius: 5px;
  border: 1px solid black;
}
