.recharts-legend-icon {
  display: none;
}
.btnSecondary {
  background-color: #edb743;
  border: 1px solid black;
  border-radius: 5px;
}
.btnSecondary:hover {
  color: black;
  border: 1px solid black;
  background-color: #f0c463;
  border-radius: 5px;
}
.btnSecondary:focus {
  color: black;
  border: 1px solid black;
  background-color: #f0c463;
  border-radius: 5px;
}
#components-grid-demo-flex-align [class~='ant-row'] {
  background: rgba(128, 128, 128, 0.08);
}
.quotaGraph {
  padding: 1em;
  margin: 1em auto;
}
.quotaStatOption {
  text-align: center;
}
.table-striped-rows {
  background-color: red;
}
.table-striped-rows tr:nth-child(2n) td {
  background-color: #fbfbfb;
}
.table-striped-rows thead {
  background-color: #f1f1f1;
}
.react-flow__connection-path {
  stroke: red;
}
.formStyle {
  padding: 3em;
  width: 70%;
  margin: auto;
  background-color: #dfdfdf;
  color: #212121;
  box-shadow: 4px 4px 12px 2px #a4a4a4;
  border-radius: 5px;
}
.hattingSequence {
  color: #000;
  background-color: #dfdfdf;
  border: 1px solid black;
  padding: 2em;
  border-radius: 5px;
  margin-bottom: 2em;
}
.hattingSequence li {
  height: 3em;
  padding: 10px;
  list-style-type: none;
  border-radius: 15px;
}
.hattingSequence li:nth-child(even) {
  background-color: #f1f1f1;
}
.hatPackSequence {
  color: #000;
  border: 1px solid gray;
  background-color: #dfdfdf;
  padding: 2em;
  border-radius: 5px;
  margin-bottom: 1em;
  margin-top: 1em;
}
.hatPackSequence li {
  height: 3em;
  padding: 10px;
  list-style-type: none;
  border-radius: 15px;
}
.hatPackSequence li:nth-child(even) {
  background-color: #f1f1f1;
}
.footer {
  left: 0;
  bottom: 0;
  width: 100%;
  text-align: center;
}
.note {
  color: #000;
  background-color: #dfdfdf;
  border: 1px solid black;
  padding: 2em;
  box-shadow: 0px 0px 15px 0px #888888;
  border-radius: 5px;
  margin-bottom: 2em;
}
.quotaGraphSelectContainer > div {
  display: inline-block;
  display: -moz-inline-box;
  *display: inline;
  /* For IE7 */
  zoom: 1;
  /* Trigger hasLayout */
  width: 33%;
  text-align: center;
}
.customNode {
  width: 200px;
  text-align: center;
  background-color: #f1f1f1;
  box-shadow: 0px 0px 15px 0px #3d3939;
  border-radius: 5px;
}
.otherFlowChartNode {
  width: 200px;
