* {
    box-sizing: border-box;
  }
  
  .Board {
    font-family: sans-serif;
    white-space: nowrap;
  }
  
  .Column {
    display: inline-block;
    vertical-align: top;
    width: 225px;
    padding: 10px 15px;
    border-radius: 5px;
    margin-right: 20px;
    background: #ffffff;
    color: #bfdbf7;
  }
  
  .Column__title {
    font-size: 18px;
    margin-bottom: 10px;
  }
  
  .Card {
    padding: 5px;
    background-color: #dfdfdf;
    color: #212121;
    box-shadow: 4px 4px 12px 2px #a4a4a4;
    border-radius: 5px;
    margin-bottom: 10px;
    cursor: move;
  }
  
  .Card--spacer,
  .Card--dragging {
    opacity: 0;
  }

  .Card--spacer {
    height: 60em;
  }
  
  .TextForm__input {
    display: block;
    background: #f4fffd;
    width: 100%;
    border-radius: 3px;
    border: none;
    font-size: 16px;
    padding: 5px;
    margin-bottom: 5px;
    opacity: 0.7;
  }
  
  .TextForm__input:focus {
    opacity: 1;
  }
  